<template>
    <div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
            <template slot="top">
                <a-button type="primary"
                          @click="handleCreate()">新增
                </a-button>
                <div style="height: 10px"></div>
                <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
                <div style="height: 10px"></div>
            </template>
        </table-component>
        <a-modal
                width="90%"
                :title="modalTitle"
                v-model="modalVisible"
                :footer="null"
                :destroyOnClose="true">
            <form-component :id="formId"/>
        </a-modal>
    </div>
</template>

<script>
    import {getUserList, deleteUser} from '@/api/user'
    import tableComponent from '@/components/table'
    import formComponent from '@/components/user/form/Index'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            filterComponent, tableComponent, formComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '姓名',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: '所属党支部',
                        dataIndex: 'department.name',
                        key: 'department.name',
                    },
                    {
                        title: '职务',
                        dataIndex: 'duty',
                        key: 'duty',
                    },
                    {
                        title: '账户类型',
                        dataIndex: 'type_format',
                        key: 'type_format',
                    },
                    {
                        title: '角色',
                        dataIndex: 'roles_format',
                        key: 'roles_format',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable: false,},
                            delete: {visible: true, disable: false,},
                        }
                    }
                    ],
                tableRequest: getUserList,
                filterFields:[
                    {label:'党支部',field:'department_id'},
                    {label:'关键字',field:'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCreate() {
                this.modalTitle = '新增文章';
                this.modalVisible = true;
                this.formId = null;
            },
            handleEdit(id) {
                this.modalTitle = '编辑文章';
                this.modalVisible = true;
                this.formId = id;
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteUser(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>

